header {
    @include clearfix();
    padding: 40px 50px 12px 38px;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    height: 114px;
    width: 100%;
    z-index: 10000;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background: linear-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed;
    background: -webkit-linear-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed;
    background: -webkit-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed;
    img {
        float: left;
        position: relative;
        top: -3px;
    }
    .logo-small {
        display: none;
    }
    .services {
        float: right;
        span {
            @include font(15px, 1.5rem);
            font-style: italic;
            color: $fontColor1;
        }
        a {
            &.btn-primary {
                margin: 0 10px 0 24px;
                &.signinLink {
                    width: 106px;
                }
            }
        }
        .signinLinkIcon {
            display: none;
        }
    }
}

@media (max-width: 992px) {
    header {
        height: 65px;
        width: 100%;
        padding: 18px 10px 17px;
        text-align: right;
        .logo {
            display: none;
        }
        .logo-small {
            display: block;
        }
        .services {
            float: none;
            a.btn {
                width: 120px;
                height: 30px;
                line-height: 30px;
                vertical-align: top;
            }
            span {
                display: none;
            }
            .signinLink {
                display: none;
            }
            .icon-user-circle {
                display: inline;
                font-size: 31px;
                top: -2px;
            }
        }
    }
}