.sidebar {
    background: linear-gradient(-13.22deg, #FF9B8F -20.98%, #FB808E 45.98%, #E65570 102.7%);
    background: -webkit-linear-gradient(-13.22deg, #FF9B8F -20.98%, #FB808E 45.98%, #E65570 102.7%);
    background: -webkit-gradient(-13.22deg, #FF9B8F -20.98%, #FB808E 45.98%, #E65570 102.7%);
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 280px;
    .logo {
        margin-top: 37px;
        margin-bottom: 10px;
        margin-left: 38px;
    }
    .btn {
        display: block;
        position: absolute;
        bottom: 40px;
        left: 40px;
        width: 121px;
    }
}

.nav {
    margin: 0;
    padding: 0;
    list-style: none;
    a {
        display: block;
        @include font(12px, 1.2rem);
        font-weight: bold;
        color: $fontColor1;
        text-transform: uppercase;
        text-decoration: none;
        position: relative;
        padding: 25px 0 23px 86px;
        &:hover {
            background-color: $bgColor4;
        }
    }
    .active {
        a {
            background-color: $bgColor4;
        }
    }
    .items {
        position: absolute;
        @include font(11px, 1.1rem);
        background: $bgColor5;
        color: $fontColor1;
        width: inherit;
        min-width: 20px;
        padding: 0 9px;
        height: 21px;
        line-height: 20px;
        border-radius: 100px;
        font-weight: bold;
        text-align: center;
        right: 20px;
        top: 23px;
        left: inherit;
    }
}

@media (max-width: 1200px) {
    .slideout-open {
        overflow: hidden;
        height: 100%;
        position: relative;
        .sidebar {
            display: block;
        }
        body {
            height: 100%;
            position: relative;
            overflow: hidden;
        }
    }
    .sidebar {
        left: auto;
        display: none;
        .logoLink {
            display: none;
        }
    }
}