.container {
    padding-left: 280px;
    padding-bottom: 50px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    & > h1 {
        display: none;
    }
}

.contentHeading {
    padding: 38px 200px 20px 48px;
    background-color: $bgColor1;
    position: relative;
    & > span {
        position: relative;
        top: -4px;
        margin-left: 5px;
    }
    .btn {
        position: absolute;
        top: 40px;
        right: 50px;
    }
    h1 {
        display: inline;
        @include font(30px, 3rem);
        font-weight: bold;
        color: $fontColor2;
        margin: 0 10px 0 0;
        overflow-x: hidden;
        text-overflow: ellipsis;
    }
    .items {
        display: inline-block;
        @include font(11px, 1.1rem);
        background: $bgColor5;
        color: $fontColor1;
        padding: 0 16px;
        height: 21px;
        line-height: 20px;
        border-radius: 100px;
        font-weight: bold;
        text-align: center;
        text-transform: uppercase;
    }
    .status {
        @include font(11px, 1.1rem);
        font-weight: bold;
        color: $fontColor3;
    }
}

.textContent {
    color: $fontColor3;
    padding: 0 50px;
    h2 {
        @include font(16px, 1.6rem);
        line-height: 26px;
        margin-bottom: 10px;
        margin-top: 0;
    }
    h3 {
        @include font(20px, 2rem);
        margin-bottom: 15px;
        margin-top: 0;
    }
    p {
        @include font(15px, 1.5rem);
        line-height: 29px;
        margin: 0 0 10px;
    }
    ul {
        padding: 0;
        @include font(13px, 1.3rem);
    }
    .row {
        @include clearfix();
    }
    .col-sm-4 {
        float: left;
        width: 33.3333%;
    }
    a {
        @include font(13px, 1.3rem);
        color: $fontColor2;
        font-weight: bold;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}

.contentNavigation {
    list-style: none;
    margin: 0;
    padding: 25px 0 0;
    li {
        display: inline-block;
        &:first-child::after {
            content: ' | ';
            color: $fontColor3;
            @include font(12px, 1.2rem);
        }
        &:first-child::before {
            content: ' ';
            color: $fontColor2;
            @include font(13px, 1.3rem);
        }
        &:last-child::after {
            content: ' ';
            color: $fontColor2;
            @include font(13px, 1.3rem);
        }
    }
    a {
        @include font(13px, 1.3rem);
        color: $fontColor2;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}

@media (min-width: 1201px) {
    .btn-hamburger, .contentHeading .logoLink {
        display: none;
    }
}

@media (max-width: 1200px) {
    .container {
        padding-left: 0;
        z-index: 9999;
        position: relative;
        background-color: $bgColor3;
        height: 100%;
        padding-top: 65px;
        & > h1 {
            display: block;
            @include font(20px, 2rem);
            font-weight: bold;
            color: $fontColor2;
            margin: 0;
            padding: 15px 15px 15px;
            background-color: $bgColor1;
            overflow-x: hidden;
            text-overflow: ellipsis;
        }
    }
    .contentHeading {
        @include clearfix();
        background: linear-gradient(9deg, #FF9B8F -45%, #FB808E 45.98%, #E65570 102.7%);
        background: -webkit-linear-gradient(9deg, #FF9B8F -45%, #FB808E 45.98%, #E65570 102.7%);
        background: -webkit-gradient(9deg, #FF9B8F -45%, #FB808E 45.98%, #E65570 102.7%);
        height: 65px;
        width: 100%;
        padding: 18px 10px 17px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        z-index: 9999;
        &.stuck {
            display: none;
        }
        & > span {
            display: none !important;
        }
        .btn {
            background-color: $bgColor1;
            color: $fontColor2;
            width: 120px;
            height: 30px;
            line-height: 30px;
            position: static;
            float: right;
            .icon-pin:before {
                color: $fontColor2;
            }
        }
        h1 {
            display: none;
        }
        .btn-hamburger {
            float: left;
            width: 40px;
            height: 30px;
            border: none;
            margin: 0 5px 0 0;
            background-color: transparent;
            vertical-align: middle;
            &:focus {
                outline: 0;
            }
        }
        .logoLink {
            float: left;
            .logo {
                margin-top: -2px;
                vertical-align: middle;
            }
        }
    }
    .textContent {
        padding: 0 15px;
    }
}

@media (max-width: 480px) {
    .contentNavigation {
        li {
            display: block;
            text-align: center;
            &:first-child:after {
                content: "";
            }
            & + li {
                margin-top: 10px;
            }
        }
    }
    .textContent {
        .col-sm-4 {
            float: none;
            width: 100%;
        }
    }
}