.infoBox {
    background-color: rgba(255, 255, 255, 0.5);
    padding: 10px 20px;
    margin: 25px 50px 20px;
    border-radius: 6px;
    @include font(13px, 1.3rem);
    color: $fontColor3;
    a {
        color: $fontColor2;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    } 
    .list-unstyled {
        padding: 0;
        margin: 0;
        list-style: none;
        line-height: 20px;
    }
}

.columnList {
    @include clearfix();
    margin: 0;
    padding: 0;
    list-style: none;
    li {
        position: relative;
        float: left;
        padding: 6px 5px 6px 46px;
        width: 50%;
        vertical-align: top;
        margin-bottom: 4px;
        text-overflow: ellipsis;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
}


@media (max-width: 1200px) {
    .container {
        & > .infoBox {
            margin-left: 15px;
            margin-right: 15px;
        }
    }
}

@media (max-width: 767px) {
    .infoBox {
        padding-left: 10px;
        padding-right: 10px;
    }
}