.country-select, .states-select, .currency-select, .status-select, .job-select, .perpage-select, .usertype-select  {
    visibility: hidden;
}

.dropdownSelect {
    position: relative;
    &.active {
        .dropdownBody {
            display: block;
        }
        button {
            border-radius: 20px 20px 0 0;
        }
        .caret {
            border-top: inherit;
            border-bottom: 7px dashed #3B2529;
        }
    }
    button {
        @include font(16px, 1.6rem);
        width: 100%;
        height: 40px;
        color: $fontColor4;
        font-style: italic;
        line-height: 20px;
        border-radius: 100px;
        background-color: rgba(255, 255, 255, 0.6);
        border: none;
        text-align: left;
        padding: 0 35px 0 25px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow-x: hidden;
        outline: 0;
        font-family: 'Noto Sans', sans-serif;
        &:hover {
            cursor: pointer;
            background-color: rgba(255, 255, 255, 1);
        }
    }
    .caret {
        position: absolute;
        right: 19px;
        top: 17px;
        cursor: pointer;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-top: 7px dashed #3B2529;
    }
    .dropdownBody {
        display: none;
        background-color: $bgColor3;
        border-radius: 0 0 20px 20px;
        position: absolute;
        left: 0;
        right: 0;
        z-index: 1000;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    .dropdownList {
        -webkit-overflow-scrolling: touch;
        max-height: 163px;
        overflow-y: auto;
        text-align: left;
        list-style: none;
        margin: 11px 0 15px;
        padding: 0;
        li {
            @include font(16px, 1.6rem);
            padding: 5px 25px;
            overflow-x: hidden;
            text-overflow:ellipsis;
            &:hover {
                cursor: pointer;
                background-color: $bgColor1;
            }
            &.selected {
                background-color: $bgColor1;
                cursor: default;
            }
        }
    }
}