.landingContainer {
    footer {
        color: $fontColor1;
        margin-top: 0;
        padding-left: 38px;
        .content {
            width: auto;
            padding: 0 10px;
        }
        hr {
            display: none;
        }
    }
    .footerMenu {
        li {
            a {
                color: $fontColor1;
                &:hover {
                    color: $fontColor1;
                }
            }
        }
    }
}

.loggedOut {
    &.accountPages {
        footer {
            padding-left: 38px;
        }
    }
}

footer {
    @include font(15px, 1.5rem);
    color: $fontColor3;
    padding: 40px 50px 43px 330px;
    margin-top: 50px;
    .content {
        @include clearfix();
        position: relative;
        padding: 0 25px;
    }
    .separator {
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 40px;
    }
}
.copyRight {
    float: left;
}

.socialIcons {
    @include clearfix();
    padding: 0;
    margin: 0;
    list-style: none;
    position: absolute;
    top: 12px;
    left: 50%;
    transform: translate(-50%, -50%);
    li {
        float: left;
        & + li {
            margin-left: 10px;
        }
        a {
            text-decoration: none;
        }
    }
}

.footerMenu {
    @include clearfix();
    float: right;
    padding: 0;
    margin: 0;
    list-style: none;
    li {
        float: left;
        a {
            color: $fontColor3;
            text-decoration: none;
            &:hover {
                color: $fontColor2;
                text-decoration: underline;
            }
        }
        & + li {
            &:before {
                content: " | ";
                margin-left: 5px;
            }
        }
    }
}

@media (max-width: 1200px) {
    .loggedOut {
        &.accountPages {
            footer {
                padding-left: 38px;
                padding-right: 50px;
            }
        }
    }
    .landingContainer {
        footer {
            padding-left: 38px;
            padding-right: 50px;
        }
    }
    footer {
        padding-left: 15px;
        padding-right: 15px;
    }
}

@media (max-width: 992px) {
    .loggedOut {
        &.accountPages {
            footer {
                padding-left: 15px;
                padding-right: 15px;
            }
        }
    }
    .landingContainer {
        footer {
            padding-left: 15px;
            padding-right: 15px;
        }
    }
    footer {
        padding: 18px 15px 17px;
        .separator {
            margin-bottom: 18px;
        }
        .content {
            padding: 0 10px;
        }
    }
}
@media (max-width: 768px) {
    footer {
        margin-top: 0;
        .content {
            text-align: center;
        }
    }
    .copyRight {
        float: none;
        display: table;
        margin: 0 auto;
    }
    .footerMenu {
        float: none;
        display: table;
        margin: 10px auto;
    }
    .socialIcons {
        position: static;
        transform: inherit;
        display: table;
        margin: 0 auto;
    }
}