#popupBackground {
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 9999;
}

#popupWrapper {
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 10000;
    overflow-y: auto;
}

.popup {
    display: none;
    position: absolute;
    margin-left: -250px;
    left: 50%;
    width: 500px;
    z-index: 2000;
    padding: 0 50px;
    text-align: center;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background: linear-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
    background: -webkit-linear-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
    background: -webkit-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
    .logo {
        margin: 50px auto 0;
        display: block;
    }
    p {
        @include font(16px, 1.6rem);
        color: $fontColor1;
        margin-bottom: 30px;
        margin-top: 0;
        line-height: 20px;
    }
    .btn {
        margin-bottom: 50px;
    }
}

@media (max-width: 500px) {
    .popup {
        width: auto;
        margin: 80px 15px !important;
        top: 0 !important;
        left: 0;
        padding: 25px 15px;
        .logo {
            margin-top: 0;
        }
        .btn {
            width: 47%;
            margin-bottom: 0;
        }
    }
}
