.wizard {
    width: 900px;
    margin: 0 auto;
    .optimize {
        display: none;
        .line {
            margin-bottom: 59px;
        }
        .form-group:first-child {
            display: none;
        }
    }
    .mask {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 9999;
    }
    form {
        margin-top: 59px;
    }
    .form-group {
        margin-bottom: 83px;
        &.disabled {
            opacity: 0.3;
        }
        .control-label {
            display: inline-block;
            @include font(30px, 3rem);
            font-style: italic;
            line-height: 38px;
            color: $fontColor2;
            cursor: pointer;
            margin-bottom: 33px;
        }
        .form-control {
            display: block;
            width: 550px;
            height: 59px;
            background-color: $bgColor3;
            border-radius: 6px;
            color: $fontColor4;
            box-shadow: none;
            font-style: italic;
        }
        textarea {
            &.form-control {
                height: 200px;
            }
        }
        .locationFields {
            float: left;
            .form-control {
                height: 40px;
                border-radius: 100px;
                margin-bottom: 10px;
            }
            .leftField {
                width: 206px;
                float: left;
            }
            .dropdownSelect {
                width: 334px;
                float: right;
                button {
                    background-color: $bgColor3;
                }
            }
        }
        .description {
            @include font(13px, 1.3rem);
            color: $fontColor3;
            width: 305px;
            float: right;
            line-height: 25px;
            margin-top: -8px;
            u {
                font-style: italic;
            }
            .btn {
                display: block;
                line-height: 36px;
                border: 2px solid #CFCFCF;
                border-radius: 100px;
                background-color: $bgColor1;
                color: #C3B4B4;
                margin-top: 23px;
                min-width: inherit;
                width: 152px;
                -webkit-box-sizing: border-box;
                -moz-box-sizing: border-box;
                box-sizing: border-box;
                cursor: pointer;
                &:hover {
                    color: #E55570;
                    border-color: #E55570;
                }
            }
        }
        .radioInput {
            .radio {
                @include font(14px, 1.4rem);
                color: $fontColor4;
                font-style: italic;
                position: relative;
                display: inline-block;
                padding-left: 43px;
                margin-right: 26px;
                margin-bottom: 15px;
                line-height: 38px;
                cursor: pointer;
                input {
                    display: none;
                }
            }
        }
        .dateField[disabled] {
            opacity: 0.5;
            cursor: default;
        }
        .dateField {
            width: 163px;
            height: 40px;
            border-radius: 100px;
            text-align: center;
            // @include font(12px, 1.2rem);
            padding: 0;
            &::-webkit-input-placeholder { // Chrome
                @include font(12px, 1.2rem);
                font-style: normal;
                font-weight: bold;
                letter-spacing: 0.1em;
            }
            &:-ms-input-placeholder { // IE 10+
                @include font(12px, 1.2rem);
                font-style: normal;
                font-weight: bold;
                letter-spacing: 0.1em;
            }
            &::-moz-placeholder { // Firefox 19+
                @include font(12px, 1.2rem);
                font-style: normal;
                font-weight: bold;
                letter-spacing: 0.1em;
            }
            &:-moz-placeholder { // Firefox 4 - 18
                @include font(12px, 1.2rem);
                font-style: normal;
                font-weight: bold;
                letter-spacing: 0.1em;
            }
        }
        & > .btn {
            margin-top: 0;
            margin-bottom: 61px;
        }
        .btn {
            @include font(12px, 1.2rem);
            min-width: 152px;
            height: 40px;
            line-height: 40px;
        }
    }
    .separator {
        margin: 0;
    }
    .icon-thumbs {
        @include font(25px, 2.5rem);
        display: inline-block;
        text-align: center;
        color: $fontColor2;
        margin-top: 48px;
        margin-bottom: 16px;
    }
    .niceJob {
        @include font(15px, 1.5rem);
        display: block;
        font-weight: bold;
        font-style: italic;
        text-align: center;
        color: $fontColor2;
    }
    p {
        @include font(18px, 1.8rem);
        font-style: italic;
        text-align: center;
        color: $fontColor3;
        line-height: 35px;
        margin: 5px 0 55px;
    }
    .employmentType {
        width: 550px;
        label {
            float: left;
            margin: 0 10px 10px 0;
            padding: 0;
        }
        input[type="checkbox"]:checked ~ .btn, input[type="radio"]:checked ~ .btn {
            background-color: $bgColor2;
            color: $fontColor1;
        }
        .btn {
            min-width: 166px;
            background-color: $bgColor3;
            color: $fontColor3;
            margin: 0;
            font-style: normal;
            @include font(12px, 1.2rem);
            &:hover {
                background-color: #ddd;
            }
        }
    }
    .salaryFields {
        width: 550px;
        .salaryType {
            label {
                margin: 0 0 10px 0;
                padding: 0;
                float: left;
                &:first-child {
                    .btn {
                        border-radius: 100px 0 0 100px;
                    }
                }
                &:last-child {
                    .btn {
                        border-radius: 0 100px 100px 0;
                    }
                }
                & + label {
                    margin-left: 3px;
                }
            }
            input[type="checkbox"]:checked ~ .btn, input[type="radio"]:checked ~ .btn {
                background-color: $bgColor2;
                color: $fontColor1;
            }
            .btn {
                min-width: 107px;
                background-color: $bgColor3;
                color: $fontColor3;
                margin: 0;
                font-style: normal;
                @include font(12px, 1.2rem);
                border-radius: 0;
                cursor: pointer;
                &:hover {
                    background-color: #ddd;
                }
            }
        }
    }
    .salaryGroup {
        margin-bottom: 20px;
        .form-control {
            @include font(25px, 2.5rem);
            color: $fontColor4;
            width: 417px;
            height: 70px;
            line-height: 70px;
            border-radius: 0 56px 56px 0;
            float: left;
        }
        .dropdownSelect {
            float: left;
            text-transform: uppercase;
            &.active {
                .caret {
                    border-top: inherit;
                    border-bottom: 7px dashed white;
                }
                button {
                    border-radius: 35px 0 0 0;
                }
            }
            button {
                @include font(18px, 1.8rem);
                color: $fontColor1;
                background-color: $bgColor2;
                width: 130px;
                height: 70px;
                line-height: 70px;
                font-weight: bold;
                font-style: italic;
                text-align: center;
                border-radius: 56px 0 0 56px;
                text-transform: uppercase;
            }
            .caret {
                border-top: 7px dashed white;
                position: absolute;
                right: 20px;
                top: 32px;
                cursor: pointer;
            }
        }
    }
    .has-error {
        .error {
            @include font(13px, 1.3rem);
            margin: 10px 0 0;
            white-space: nowrap;
        }
    }
    .checkbox {
        label {
            input {
                display: block;
                position: absolute;
                left: 50%;
            }
            .btn {
                z-index: 100;
                position: relative;
            }
        }
    }
    .textBoxGroup {
        position: relative;
        .subtext {
            @include font(12px, 1.2rem);
            color: $fontColor3;
            position: absolute;
            top: -20px;
            right: 0;
        }
    }
}