header {
    .btn {
        .icon-pin {
            @include font(20px, 2rem);
            display: inline-block;
            font-style: normal;
            position: relative;
            top: 3px;
            left: -7px;
            &:before {
                color: $fontColor2;
            }
        }
        .icon-stack-layers {
            @include font(20px, 2rem);
            display: inline-block;
            font-style: normal;
            position: relative;
            top: 3px;
            left: -7px;
        }
        &:hover {
            &:before {
                color: $fontColor1;
            }
        }
    }
    .services {
        position: relative;
        .icon-user-circle {
            @include font(42px, 4.2rem);
            color: $fontColor1;
            text-decoration: none;
            position: relative;
            vertical-align: middle;
            margin-right: 10px;
            top: -1px;
            &:hover {
                opacity: 0.8;
            }
        }
    }
}
.introText, .benefitsText, .closingCta {
    .icon {
        @include font(25px, 2.5rem);
        display: inline-block;
        font-style: normal;
        position: relative;
        top: 6px;
        left: -13px;
    }
    .btn-white {
        .icon:before {
            color: $fontColor2;
        }
        &:hover {
            &:before {
                color: $fontColor1;
            }
        }
    }
}
.sidebar {
    .btn {
        .icon-peace-sign {
            display: inline-block;
            margin-right: 9px;
            position: relative;
            top: 2px;
            left: -2px;
            width: 17px;
            height: 21px;
            @include font(21px, 2.1rem);
        }
    }
}

.nav {
    .icon {
        display: block;
        @include font(27px, 2.7rem);
        position: absolute;
        left: 41px;
        top: 18px;
        background-repeat: no-repeat;
        &.icon-profile-contact-info {
            top: 17px;
        }
    }
}

.contentHeading {
    .btn {
        .icon-pin {
            @include font(20px, 2rem);
            display: inline-block;
            position: relative;
            top: 3px;
            left: -7px;
            height: 20px;
            width: 20px;
        }
    }
}

.dashboard {
    .content {
        h2 {
            .icon-star {
                display: inline-block;
                color: #E8C772;
                @include font(18px, 1.8rem);
                vertical-align: middle;
                position: relative;
                top: -3px;
                margin-right: 4px;
            }
        }
    }
}

.checkbox {
    text-align: left;
    &.oneline {
        .icon-checked {
            top: -7px;
        }
    }
    &.longline {
        .icon-checked {
            top: -7px;
        }
    }
    label {
        input[type="checkbox"]:checked ~ .icon-checked {
            .path2, .path3 {
                display: inline;
            }
        }
        .icon-checked {
            display: block;
            height: 30px;
            width: 30px;
            @include font(30px, 3rem);
            position: absolute;
            left: 0;
            top: 2px;
            .path2, .path3 {
                display: none;
            }
        }
    }
}
.radioInput {
    text-align: left;
    .radio {
        input[type="radio"]:checked ~ .icon-radio {
            .path2 {
                display: inline;
            }
        }
        .icon-radio {
            display: block;
            height: 30px;
            width: 30px;
            @include font(30px, 3rem);
            position: absolute;
            left: 0;
            top: 2px;
            .path2 {
                display: none;
            }
        }
    }
}

.icon-hint {
    width: 28px;
    height: 28px;
    position: absolute;
    right: 6px;
    top: 5px;
    font-size: 28px;
    cursor: pointer;
    .path2 {
        position: relative;
    }
}

h3 {
    .icon {
        position: absolute;
        font-size: 27px;
        left: 0;
        top: -6px;      
    }
}

.form-group {
    .btn {
        .icon-high {
            display: inline-block;
            vertical-align: middle;
            margin-right: 12px;
            position: relative;
            top: -3px;
            width: 25px;
            height: 25px;
            font-size: 25px;
        }
    }
}

.infoBox {
    .columnList {
        .icon {
            @include font(27px, 2.7rem);
            color: $fontColor2;
            position: absolute;
            left: 1px;
            top: 50%;
            margin-top: -14px;
            &.icon-briefcase {
                
            }
            &.icon-history {
                left: 2px;
            }
            &.icon-globe {
                left: 2px;
            }
            &.icon-envelope {
                margin-top: -15px;
            }
            &.icon-map-marker {
                left: 5px;
            }
            &.icon-money {
                margin-top: -15px;
                left: 1px;
            }
            &.icon-close {
                left: 3px;
            }
        }
    }
}

.icon-cross {
    font-size: 28px;
    .path1 {
        &:before {
            content: $icon-cross-path1;  
            color: rgba(105, 67, 67, 0.3);  
            opacity: 1;
        }
    }
}

.alert {
    .icon-cross {
        position: relative;
        top: -5px;
    }
}

.socialIcons {
    @include font(27px, 2.7rem);
    .icon {
        &:hover {
            .path1:before {
                color: $bgColor7;
            }
        }
    }
}

.thankyouMsg {
    .icon-check {
        @include font(27px, 2.7rem);
        color: $fontColor1;
        position: absolute;
        left: -35px;
        top: -2px;
    }
}