.introText {
    width: 580px;
    &.firstSlide {
        padding-top: 159px;
        h2 {
            @include font(72px, 7.2rem);
            line-height: 98px;
        }
        p {
            @include font(20px, 2rem);
            line-height: 33px;
        }
    }
    &.secondSlide {
        padding-top: 140px;
    }
    &.thirdSlide {
        padding-top: 95px;
    }
    h2 {
        color: $fontColor1;
        @include font(52px, 5.2rem);
        font-style: italic;
        font-weight: bold;
        line-height: 71px;
        z-index: 10;
        margin: 0;
    }
    p {
        @include font(18px, 1.8rem);
        color: $fontColor1;
        line-height: 30px;
        margin: 39px 0 62px;
        span {
            font-weight: bold;
            font-style: italic;
        }
    }
}

.indicators {
    position: fixed;
    text-align: center;
    right: 40px;
    top: 50%;
    margin-top: -34px !important;
    z-index: 2000;
    -moz-user-select: none;
    -webkit-user-select: none;
    ul {
        padding: 0;
        margin: 0;
        list-style: none;
        li {
            display: none;
            &:nth-child(-n + 3) {
                display: block;
            }
        }
    }
    a {
        display: block;
        position: relative;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: $bgColor1;
        margin: 8px 0;
        cursor: pointer;
        opacity: 0.3;
        &.active, &.focus {
            opacity: 1;
        }
    }
}

@media (max-width: 1599px) {
    .introText {
        &.thirdSlide {
            padding-top: 55px;
        }
    }
}

@media (max-width: 1366px) {
    .introText {
        &.firstSlide {
            padding-top: 100px;
            h2 {
                @include font(36px, 3.6rem);
                line-height: 49px;
            }
        }
        &.secondSlide {
            padding-top: 75px;
        }
        &.thirdSlide {
            padding-top: 25px;
        }
        h2 {
            color: $fontColor1;
            @include font(30px, 3rem);
            font-style: italic;
            font-weight: bold;
            line-height: 36px;
            z-index: 10;
            margin: 0;
        }
        p {
            margin: 30px 0 40px;
        }
    }
}

@media (max-width: 992px) {
    .introText {
        width: 380px;
        &.firstSlide, &.secondSlide, &.thirdSlide {
            h2 {
               @include font(26px, 2.6rem); 
            }
            p {
                @include font(14px, 1.4rem);
                line-height: 26px;
                margin: 10px 0 20px;
            }
        }
        &.thirdSlide {
            padding-top: 60px;
        }
        p {
            br {
                display: none;
            }
        }
    }
}

@media (max-width: 767px) {
    .introText {
        width: 100%;
        float: none;
        &.firstSlide {
            padding-top: 75px;
        }
        &.secondSlide {
            padding-top: 10px;
        }
        &.thirdSlide {
            padding-top: 10px;
            & ~ .polygonShape {
                margin-bottom: 25px;
            }
        }
        h2 {
            color: $fontColor1;
            line-height: 36px !important;
        }
        p {
            line-height: 22px !important;
        }
    }
    .indicators {
        display: none !important;
    }
}