.landingPage {
    background: linear-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed;
    background: -webkit-linear-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed;
    background: -webkit-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed;
}
.landingContainer {
    .benefits {
        background-color: $bgColor1;
    }
    .content {
        @include clearfix();
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
        padding-top: 114px;
        width: 1120px;
        margin-left: auto;
        margin-right: auto;
    }
    .btn {
        height: 45px;
        width: 154px;
        line-height: 45px;
    }
}

@import "_shapeWrapper/shapeWrapper";
@import "_intro/intro";
@import "_benefits/benefits";
@import "_closingCta/closingCta";

.fp-auto-height.fp-section,
.fp-auto-height .fp-slide,
.fp-auto-height .fp-tableCell {
    height: auto !important;
}

.fp-responsive .fp-auto-height-responsive.fp-section,
.fp-responsive .fp-auto-height-responsive .fp-slide,
.fp-responsive .fp-auto-height-responsive .fp-tableCell {
    height: auto !important;
}

@media (max-width: 1366px) {
    .landingContainer {
        .content {
            width: 920px;
        }
    }
}

@media (max-width: 992px) {
    .landingContainer {
        .content {
            width: 718px;
            padding-top: 65px;
        }
    }
}

@media (max-width: 767px) {
    .landingContainer {
        .content {
            width: 100%;
            padding: 0 25px;
        }
    }
}
