.form-group {
    margin-bottom: 10px;
    position: relative;
    &:last-child {
        margin-bottom: 0;
    }
    .form-control {
        font-family: 'Noto Sans', sans-serif;
        @include font(16px, 1.6rem);
        cursor: auto !important;
        height: 40px;
        color: $fontColor2;
        line-height: 20px;
        width: 100%;
        border-radius: 100px;
        background-color: rgba(255, 255, 255, 0.6);
        &:hover {
            cursor: pointer;
            background-color: rgba(255, 255, 255, 1);
        }
        &::-webkit-input-placeholder { // Chrome
            @include font(16px, 1.6rem);
            font-style: italic;
            font-family: 'Noto Sans', sans-serif;
            color: $fontColor4;
        }
        &:-ms-input-placeholder { // IE 10+
            @include font(16px, 1.6rem);
            font-style: italic;
            font-family: 'Noto Sans', sans-serif;
            color: $fontColor4;
        }
        &::-moz-placeholder { // Firefox 19+
            @include font(16px, 1.6rem);
            font-style: italic;
            font-family: 'Noto Sans', sans-serif;
            color: $fontColor4;
            opacity: 1;
        }
        &:-moz-placeholder { // Firefox 4 - 18
            @include font(16px, 1.6rem);
            font-style: italic;
            font-family: 'Noto Sans', sans-serif;
            color: $fontColor4;
            opacity: 1;
        }
    }
    textarea {
        &.form-control {
            height: 200px;
            resize: none;
            padding-top: 10px;
            padding-bottom: 10px;
            padding-left: 24px;
            overflow-y: auto;
            border-radius: 20px;
        }
    }
    .btn {
        @include font(14px, 1.4rem);
        line-height: 55px;
        width: inherit;
        height: 55px;
        min-width: 215px;
        margin-top: 40px;
    }
    &.leftField {
        width: 49%;
        float: left;
    }
    &.rightField {
        width: 49%;
        float: right;
    }
    .tooltip {
        color: $fontColor3;
        display: none;
        width: 251px;
        padding-left: 28px;
        padding-top: 15px;
        padding-bottom: 19px;
        padding-right: 10px;
        position: absolute;
        right: -231px;
        top: 50px;
        background-color: #F6F6F6;
        border-radius: 12px;
        text-align: left;
        @include font(13px, 1.3rem);
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        z-index: 1000;
        box-shadow: 1px 1px 2px #999;
    }
}

@media (max-width: 1366px) {
    .form-group {
        .btn {
            margin-top: 20px;
        }
    }
}

@media (min-width: 768px) {
    .form-group {
        .icon-hint {
            &:hover ~ .tooltip {
                display: block !important;
            }
        }
    }
}

@media (max-width: 767px) {
    .form-group {
        .btn {
            margin-top: 5px;
        }
        .tooltip {
            width: 100%;
            left: 0;
            right: inherit;
        }
    }
}

@media (max-width: 480px) {
    .form-group {
        &.leftField, &.rightField {
            float: none;
            width: 100%;
        }
        &:last-child {
            margin-bottom: 10px;
        }
    }
}