.applicantsFilter {
    background-color: $bgColor1;
    padding: 5px 50px 32px;
    &.updateCandidate {
        background-color: $bgColor3;
        .dropdownSelect {
            button {
                background-color: $bgColor1;
            }
        }
        .dropdownBody {
            background-color: $bgColor1;
        }
        .dropdownList {
            li {
                &.selected {
                    background-color: $bgColor3;
                    cursor: default;
                }
            }
        }
    }
    .control-label {
        font-style: italic;
        @include font(14px, 1.4rem);
        position: absolute;
        left: 20px;
        top: 10px;
        z-index: 1;
        color: $fontColor4;
    }
    select {
        &.form-control {
            width: 254px;
        }
        &.perpage-select {
            width: 154px;
        }
    }
    .dropdownSelect {
        width: 254px;
        .dropdownBody {
            box-shadow: 4px 5px 5px #ccc;
        }
        &.active {
            button {
                background-color: $bgColor1;
                border: 1px solid #f6f6f6;
                border-bottom: 0;
                padding-bottom: 1px;
                padding-left: 68px;
                &:hover {
                    border-bottom: 0;
                }
            }
        }
        button {
            background-color: #f6f6f6;
            color: $fontColor2;
            padding-left: 69px;
            @include font(14px, 1.4rem);
            font-family: 'Noto Sans', sans-serif;
            font-style: normal;
            font-weight: bold;
            &:hover {
                background-color: $bgColor1;
                border: 1px solid #f6f6f6;
                padding-left: 68px;
            }
        }
        .dropdownList {
            li {
                @include font(14px, 1.4rem);
            }
        }
    }
    .form-group {
        float: left;
        margin-bottom: 0;
        .job-select ~ .dropdownSelect {
            &.active {
                button {
                    padding-left: 55px;
                }
            }
            button {
                padding-left: 56px;
                &:hover {
                    padding-left: 55px;
                    padding-right: 34px;
                }
            }
        }
        .perpage-select ~ .dropdownSelect {
            width: 154px;
            &.active {
                button {
                    padding-left: 85px;
                }
            }
            button {
                padding-left: 86px;
                &:hover {
                    padding-left: 85px;
                }
            }
        }
        .btn {
            margin-top: 0;
            &.btn-primary {
                @include font(12px, 1.2rem);
                font-family: 'Noto Sans', sans-serif;
                background-color: $bgColor9;
                min-width: 90px;
                width: inherit;
                height: 40px;
                line-height: 40px;
                &:hover {
                    background-color: $bgColor10;
                }
            }
        }
    }
    .clear {
        font-family: Arial, Helvetica, sans-serif;
        @include font(20px, 2rem);
        color: $fontColor6;
        -webkit-appearance: none;
        background: transparent;
        font-weight: bold;
        line-height: 20px;
        border: 0;
        padding: 0 1px;
        margin-top: 5px;
        outline: 0;
        text-decoration: none;
        display: block;
        &:hover {
            .icon-cross {
                .path1 {
                    &:before {
                        color: $fontColor7;
                    }
                }
            }
        }
    }
}

@media (max-width: 1200px) {
    .applicantsFilter {
        padding: 0 15px 15px;
    }
}

@media (max-width: 991px) {
    .myApplicants {
        .applicantsFilter {
            .form-group {
                &:nth-child(4) {
                    clear: both;
                    margin-top: 10px;
                }
                .search {
                    margin-top: 10px;
                }
            }    
        }
    }
}

@media (max-width: 767px) {
    .applicantsFilter {
        padding-bottom: 15px;
        @include clearfix();
        .form-group {
            width: 100%;
            margin-right: 0;
            float: none;
            & + .form-group {
                margin-top: 10px;
            }
            &:last-child {
                margin-bottom: 0;
            }
            .clear {
                display: none;
            }
            .btn {
                margin-top: 0 !important;
                width: 100%;
            }
        }
        .btn {
            margin-top: 10px;
            min-width: inherit;
            width: 49%;
            float: left;
            margin-right: 0 !important;
            & + .btn {
                margin-left: 2%;
            }
        }
        .dropdownSelect {
            width: 100% !important;
        } 
    }
}