/* @todo Kad. this code need to be removed after implementing main version of post a job wizard */
.postJob {
    &.version-2 {
        padding-top: 0;
        .wizard {
            .salaryFields {
                .salaryType {
                    .btn {
                        min-width: 181px;
                    }
                }
            }
            .salaryGroup {
                .form-control {
                    border-radius: 56px;
                    width: 550px;
                }
            }
            .locationFields {
                width: 550px;
                .leftField {
                    .form-control {
                        width: 100%;
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
}
@media (max-width: 1200px) {
    .postJob {
        &.version-2 {
            padding-top: 65px;
        }
    }
}
@media (max-width: 991px) {
    .postJob {
        &.version-2 {
            .wizard {
                .employmentType {
                    label {
                        width: 32%;
                        margin: 0;
                        & + label {
                            margin-left: 2%;
                        }
                        .btn {
                            width: 100%;
                            min-width: inherit;
                        }
                    }
                }
                .salaryFields {
                    .form-control {
                        width: 100%;
                    }
                    label {
                        width: 33.333%;
                        margin: 0;
                        .btn {
                            width: 100%;
                            min-width: inherit !important;
                        }
                    }
                }
                .locationFields {
                    width: 64%;
                }
            }
        }
    }
}
@media (max-width: 768px) {
    .postJob {
        &.version-2 {
            .wizard {
                .employmentType {
                    label {
                        .btn {
                            @include font(11px, 1.1rem);
                        }
                    }
                }
                .locationFields {
                    width: 100%;
                }
            }
        }
    }
}