.table-responsive {
    padding: 0 50px;
    min-height: .01%;
    -webkit-overflow-scrolling: touch;
}

.genericTable {
    @include font(13px, 1.3rem);
    color: $fontColor3;
    border-spacing: 0 5px;
    margin: 25px 0 20px;
    width: 100%;
    table-layout: fixed;
    &.myJobs {
        table-layout: inherit;
        td {
            vertical-align: middle;
            &:last-child {
                overflow-x: inherit;
            }
            &.title {
                max-width: 200px;
            }
            &.statuses {
                max-width: 174px;
                width: 174px;
            }
        }
    }
    &.resumeResults {
        table-layout: inherit;
        margin-top: 0 !important;
        th {
            min-width: 110px;
        }
        td {
            vertical-align: top;
        }
    }
    &.dataTable {
        th {
            position: relative;
            cursor: pointer;
            &.no-sort {
                cursor: default;
            }
            .sortingIcons {
                display: inline;
                position: absolute;
                top: 50%;
                margin-top: -7px;
                margin-left: 7px;
                right: inherit;
                .caret {
                    display: block;
                    position: relative;
                    opacity: 0.5;
                    &.dropdown {
                        border-top: 0;
                        border-bottom: 5px dashed;
                    }
                    &.dropup {
                        top: 3px;
                    }
                }
            }
            &.sorting_asc {
                .dropdown {
                    opacity: 1 !important;
                }
            }
            &.sorting_desc {
                .dropup {
                    opacity: 1 !important;
                }
            }
        }
    }
    &.applicantsJobCount {
        th {
            padding-right: 20px;
            .sortingIcons {
                right: 0;
            }
        }
    }
    & * {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    tr {
        td:first-child {
            border-top-left-radius: 6px;
            border-bottom-left-radius: 6px;
        }
        td:last-child {
            border-top-right-radius: 6px;
            border-bottom-right-radius: 6px;
        }
        &.new {
            td {
                background-color: $bgColor1;
                &:first-child {
                    padding-left: 41px;
                }
                &:nth-child(4) {
                    font-weight: bold;
                }
            }
        }
        
    }
    th {
        @include font(12px, 1.2rem);
        font-weight: bold;
        color: $fontColor5;
        text-align: left;
        padding: 10px;
        white-space: nowrap;
        text-transform: uppercase;
        &:first-child {
            padding-left: 20px;
        }
    }
    td {
        background-color: rgba(255, 255, 255, 0.5);
        padding: 20px 10px;
        position: relative;
        text-overflow: ellipsis;
        overflow-x: hidden;
        vertical-align: top;
        &.name {
            font-weight: bold;
        }
        &.nowrap {
            white-space: nowrap;
        }
        &.created {
            @include font(12px, 1.2rem);
            font-style: italic;
        }
        .activation {
            font-weight: bold;
        }
        &.statuses {
            padding: 0 !important;
            text-align: center;
            position: relative;
            background-color: $bgColor1;
            height: 80px;
            vertical-align: top;
            & > a {
                @include font(11px, 1.1rem);
                font-weight: bold;
                text-transform: uppercase;
                border-top-right-radius: 6px;
                border-bottom: 1px solid #f6f6f6;
                width: 174px;
                height: 50%;
                padding: 0 !important;
                vertical-align: middle;
                margin: auto 0;
                display: inline-block;
                &:after {
                    content:"";
                    display: inline-block;
                    height: 100%;
                    vertical-align: middle;
                }
            }
            a {
                display: block;
                padding: 11px 12px;
                text-decoration: none;
                &:hover {
                    background-color: $bgColor2;
                    color: $fontColor1;
                }
            }
            .statusSelect {
                height: 50%;
                width: 174px;
                cursor: pointer;
                border-radius: 0 0 6px 0;
                &.expanded {
                    border-radius: 0;
                    background-color: $bgColor2;
                    .currentStatus {
                        color: $fontColor1;
                        &:hover {
                            border-radius: 0;
                        }
                    }
                    .statusOptions {
                        display: block;
                        a {
                            color: $fontColor3;
                            border-top: 1px solid #F6F6F6;
                            &:hover {
                                color: $fontColor1;
                            }
                        }
                    }
                    .caret {
                        border-top: inherit;
                        border-bottom: 5px dashed;
                    }
                }
                &:hover {
                    background-color: $bgColor2;
                    .currentStatus {
                        color: $fontColor1;
                    }
                    border-radius: 0 0 5px 0;
                }
            }
            .currentStatus {
                color: rgba(110, 96, 99, 0.7);
                position: relative;
                height: 100%;
                padding: 0;
                @include font(11px, 1.1rem);
                font-weight: bold;
                text-transform: uppercase;
                width: 100%;
                .caret {
                    position: absolute;
                    right: 15px;
                    top: 50%;
                    margin-top: -3px;
                }
                &:after {
                    content:"";
                    display: inline-block;
                    height: 100%;
                    vertical-align: middle;
                }
            }
            .statusOptions {
                background-color: $bgColor1;
                display: none;
                position: absolute;
                left: 0;
                right: 0;
                top: 100%;
                box-shadow: 4px 5px 5px #ccc;
                z-index: 100;
                border-radius: 0 0 6px 6px;
                overflow: hidden;
            }
            .optionSelected {
                width: 174px;
                height: 100%;
                border-top-right-radius: 6px;
                &:after {
                    content:"";
                    display: inline-block;
                    height: 100%;
                    vertical-align: middle;
                }
            }
        }
        &:first-child {
            padding-left: 20px;

        }
        &:last-child {
            padding-right: 20px;
        }
    }
    .dot {
        display: inline-block;
        width: 8px;
        height: 8px;
        border-radius: 100%;
        background-color: $bgColor5;
        position: absolute;
        left: 20px;
        top: 25px;
    }
    .items {
        position: absolute;
        display: inline-block;
        @include font(11px, 1.1rem);
        background: $bgColor5;
        color: $fontColor1 !important;
        padding: 0 16px;
        height: 21px;
        line-height: 20px;
        border-radius: 100px;
        font-weight: bold;
        text-align: center;
        margin-left: 10px;
        top: 30px;
        text-transform: uppercase;
    }
    a {
        color: $fontColor3;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
    .jobTitle {
        width: 21%;
    }
}

.dashboard {
    .table-responsive {
        width: calc(100% - 395px);
        margin: -5px 17px 15px 0;
        float: left;
        padding: 0;
        .genericTable {
            margin: 0;
        }
    }
}

.statusList {
    float: right;
    @include font(13px, 1.3rem);
    color: $fontColor3;
    background-color: $bgColor1;
    border-radius: 6px;
    width: 378px;
    border-spacing: 0;
    padding: 12px 40px 11px;
    margin-bottom: 20px;
    tr:last-child {
        td {
            border-bottom: none;
        }
    }
    td {
        padding: 13px 0;
        border-bottom: 1px solid #ddd;
        &:last-child {
            text-align: right;
            padding-right: 15px;
        }
    }
}

@media (max-width: 1200px) {
    .table-responsive {
        padding: 0;
        overflow-x: auto;
        margin: 0 15px;
    }
    .dashboard {
        .table-responsive {
            width: 100%;
        }
        .statusList {
            width: 100%;
        }
    }
}

@media (min-width: 992px) {
    // for hovering tables
    .genericTable {
        tr {
            &:hover {
                &.new {
                    td:first-child {
                        padding-left: 40px;
                    }
                }
                td {
                    border-top: 1px solid #FB808E;
                    border-bottom: 1px solid #FB808E;
                    background-color: $bgColor1;
                    padding-top: 19px;
                    padding-bottom: 19px;
                    &:first-child {
                        border-left: 1px solid #FB808E;
                        padding-left: 19px;
                    }
                    &:last-child {
                        border-right: 1px solid #FB808E;
                        padding-right: 19px;
                    }
                    &.name {
                        color: $fontColor2;
                    }
                    a {
                        color: $fontColor2;
                    }
                }
                .dot {
                    left: 19px;
                    top: 24px;
                }
                .items {
                    top: 29px;
                }
            }
        }
        // fixing browser space compatiblity issue on my jobs page
        &.myJobs {
            tr {
                &:hover {
                    td {
                        border: none !important;
                        // padding: inherit !important;
                        box-shadow: 1px 0 0 1px #FB808E;
                        background-color: $bgColor1;
                        &:first-child {
                            box-shadow: 0 0 0 1px #FB808E;
                            padding-left: 20px;
                        }
                        &:last-child {
                            box-shadow: 1px 1px 0 #FB808E;
                            padding-right: 20px;
                        }
                        &.statuses {
                            & > a, .optionSelected {
                                box-shadow: 0px -1px 0 #FB808E;
                            }
                            .statusSelect.expanded {
                                border-radius: 0 !important;
                            }
                        }
                    }
                    .dot {
                        left: 20px;
                        top: 25px;
                    }
                    .items {
                        top: 30px;
                    }
                }
            }
        }
    }    
}

@media (max-width: 991px) {
    .genericTable {
        &.myJobs {
            td {
                &.title {
                    min-width: 250px;
                }
                &.applicants {
                    min-width: 130px;
                }
                &.location {
                    min-width: 150px;
                }
                &.created {
                    min-width: 100px;
                }
                &.refresh {
                    min-width: 150px;
                }
            }
        }
    }
    .accountPages {
        .genericTable {
            table-layout: inherit;
            margin-top: 5px;
        }
    }

    .statusList {
        padding: 5px 15px;
        td {
            padding: 10px 0;
            &:last-child {
                padding-right: 0;
            }
        }
    }
}

@media (max-width: 480px) {
    .genericTable {
        &.resumeResults {
            td:nth-child(2) {
                display: block;
                width: 300px;
            }
        }
    }
}