.paginationList {
	list-style: none;
	font-weight: bold;
	line-height: 20px;
	padding-left: 0;
	margin: 0 50px;
	li {
		display: inline-block;
		margin-right: 1px;
		a {
			display: block;
			@include font(14px, 1.4rem);
			color: $fontColor3;
			background-color: rgba(255, 255, 255, 0.5);
			text-decoration: none;
			padding: 10px 16px;
			border-radius: 6px;
			&:hover {
				color: $fontColor1;
				background-color: $bgColor2;
			}
		}
		&.active {
			a {
				color: $fontColor1;
				background-color: $bgColor2;
				cursor: default;
			}
		}
	}
}

@media (max-width: 1200px) {
	.paginationList {
		margin: 0 15px;
	}
}

@media (max-width: 480px) {
	.paginationList {
		margin-top: 20px;
		li {
			a {
				padding: 5px 11px;
			}
		}
	}
}