.progressBar {
    @include font(12px, 1.2rem);
    height: 60px;
    background: rgba(246, 246, 246, 0.9);
    z-index: 9999;
    color: $fontColor3;
    font-weight: bold;
    text-transform: uppercase;
    position: fixed;
    top: 0;
    left: 280px;
    right: 0;
    text-align: center;
    line-height: 60px;
    .close {
        top: 15px;
        left: 20px;
        right: inherit;
        text-align: left; // for ie
    }
}

.progress {
    @include clearfix();
    display: inline-block;
    height: 10px;
    overflow: hidden;
    margin-left: 8px;
    position: relative;
    vertical-align: middle;
    .half {
        width: 159px;
        height: 100%;
        background-color: $bgColor8;
        float: left;
        &:first-child {
            margin-right: 2px;
            border-radius: 130px 0 0 130px;
        }
        &:nth-child(2n) {
            margin-right: 2px;
            border-radius: 0 130px 130px 0;
        }
    }
    span {
        position: absolute;
        background-color: $bgColor2;
        border-radius: 130px;
        left: 0;
        top: 0;
        bottom: 0;
    }
    i {
        width: 2px;

    }
}
