@media only screen and (max-device-width: 480px) {
    html {
        -webkit-text-size-adjust: none;
    }
}

html {
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0); // for removing highlight beckground for mobile tap 
    -webkit-tap-highlight-color: transparent; // i.e. Nexus5/Chrome and Kindle Fire HD 7
    font-size: 62.5%;
    min-width: 100%;
    min-height: 100%;
    overflow-y: scroll;
}

body {
    font-family: 'Noto Sans', sans-serif;
    background-repeat: no-repeat;
    &.accountPages {
        background-color: $bgColor3;
        & > .container {
            min-height: 600px;
        }
    }
}

hr.separator {
    opacity: 0.1;
    border-top: 1px solid #000000;
    margin: 20px 50px;
}
@media (max-width: 1200px) {
    hr.separator {
        margin-left: 15px;
        margin-right: 15px;
    }
}

.smallSpace {
    margin-top: 10px;
    margin-bottom: 10px;
}
.topSpace {
    margin-top: 20px !important;
}
.smallTopSpace {
    margin-top: 10px !important;
}
.bottomSpace {
    margin-bottom: 20px !important;
}
.smallBottomSpace {
    margin-bottom: 10px !important;
}
.rightSpace {
    margin-right: 10px !important;
}
.noSpace {
    margin: 0 !important;
}
.noTopSpace {
    margin-top: 0 !important;
}
.noBottomSpace {
    margin-bottom: 0 !important;
}
.noLeftSpace {
    padding-left: 0;
}
.noRightSpace {
    padding-right: 0;
}
.noBorder {
    border: none;
}
.hideElement {
    display: none;
}
.noStrong {
    font-weight: normal;
}
.strong {
    font-weight: bold;
}
.clear {
    clear: both;
}
.breakWord {
    word-wrap: break-word;
}
.clearfix {
    &:before,
    &:after {
        content: " ";
        display: table;
    }
    &:after {
        clear: both;
    }
}
.pull-left {
    float: left;
}
.pull-right {
    float: right;
}
.textCenter {
    text-align: center;
}
.list-unstyled {
    list-style: none;
}
.centerBlock {
    display: table;
    margin-left: auto;
    margin-right: auto;
}
.hot {
    color: $fontColor2 !important;
}
// active current style for pagination
.sr-only {
    position: absolute;
    overflow: hidden;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    clip: rect(0, 0, 0, 0);
    border: 0;
}
.icon-bar {
    display: block;
    width: 24px;
    height: 2px;
    border-radius: 1px;
    background-color: $bgColor1;
    & + .icon-bar {
        margin-top: 5px;
    }
}

.caret {
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: 2px;
    vertical-align: middle;
    border-top: 5px dashed;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
}

.sign-reg, .wizard {
    .alert {
        margin-left: 0;
        margin-right: 0;
    }
}
.alert {
    @include font(14px, 1.4rem);
    color: $fontColor3;
    padding: 15px 55px 15px 15px;
    border-radius: 6px;
    margin: 25px 50px;
    position: relative;
    a {
        color: $fontColor2;
        &:hover {
            text-decoration: underline;
        }
    }
    .close {
        opacity: 1;
        &:hover {
            opacity: 0.6;
        }
    }
}
.alert-success {
    color: #6E6063;
    background-color: rgba(255, 255, 255, 0.5);
    border: 1px solid #6E6063;
    .icon-cross .path1:before {
        color: #6E6063;
    }
}
.alert-info {
    color: #6E6063;
    background-color: rgba(255, 255, 255, 0.5);
    border: 1px solid #F2E991;
}
.alert-warning {
    color: #6E6063;
    background-color: #F2E991;
    border: 1px solid #F2E991;
}
.alert-danger {
    color: #E55570;
    background-color: rgba(255, 255, 255, 0.5);
    border: 1px solid #E55570;
    .icon-cross .path1:before {
        color: #E55570;
    }
}
@media (max-width: 1200px) {
    .alert {
        margin-left: 15px;
        margin-right: 15px;
    }
}

.close {
    font-family: Arial, Helvetica, sans-serif;
    @include font(20px, 2rem);
    color: $fontColor3;
    -webkit-appearance: none;
    background: transparent;
    font-weight: bold;
    line-height: 20px;
    opacity: 0.6;
    border: 0;
    padding: 0;
    position: absolute;
    right: 15px;
    top: 15px;
    &:hover {
        opacity: 1;
    }
    &:focus {
        background: none;
        border: none;
        outline: 0;
    }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}
