.formWrapper {
    padding: 50px 85px 67px;
    text-align: center;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background: linear-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
    background: -webkit-linear-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
    background: -webkit-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
    p {
        @include font(16px, 1.6rem);
        color: $fontColor1;
        line-height: 31px;
        margin: 0;
    }
    .btn-primary {
        background-color: rgba(196, 71, 94, 0.6);
        &:hover {
            background-color: $bgColor7;
        }
    }
    .infoBox {
        text-align: left;
        margin-left: 0;
        margin-right: 0;
    }
}

.loggedOut {
    &.accountPages {
        background-color: transparent !important;
        .container {
            background-color: transparent !important;
            width: 969px;
            margin: 0 auto;
            padding-top: 140px;
            padding-left: 0;
            & > h1 {
                display: block;
                @include font(40px, 4rem);
                color: $fontColor2;
                text-align: center;
                line-height: 54px;
                margin: 0;
                padding: 0;
            }
        }
    }
    .innerForms {
        .contentHeading {
            display: none;
        }
        .formWrapper {
            margin: 0;
            h1 {
                display: block;
            }
        }
    }
    .alert {
        margin: 0 0 25px 0;
    }
}

.innerForms {
    .formWrapper {
        margin: 25px 50px;
        border-radius: 20px;
        h1 {
            display: none;
            @include font(30px, 3rem);
            color: $fontColor1;
            margin-top: 0;
            margin-bottom: 15px;
        }
        p {
            text-align: left;
        }
    }
}

.not-found, .forgotPassword {
    background-image: url("../../images/empl/cover.svg");
    background-size: cover;
}
.not-found {
    .formWrapper {
        padding-left: 50px;
        padding-right: 50px;
    }
}
.sign-reg, .not-found, .forgotPassword {
    position: relative;
    padding-top: 112px;
    padding-bottom: 101px;
    .cover {
        background-size: cover;
        background-image: url("../../images/empl/cover.svg");
        -webkit-animation: fadein 4s; /* Safari, Chrome and Opera > 12.1 */
       -moz-animation: fadein 4s; /* Firefox < 16 */
        -ms-animation: fadein 4s; /* Internet Explorer */
         -o-animation: fadein 4s; /* Opera < 12.1 */
            animation: fadein 4s;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: -1;
    }
    .formWrapper {
        width: 746px;
        margin: 0 auto;
        h1, h2 {
            @include font(19px, 1.9rem);
            color: $fontColor1;
            font-weight: bold;
            margin-top: 50px;
            margin-bottom: 15px;
        }
        h1 {
            margin-top: 0;
        }
        .logo {
            display: none;
        }
        h3 {
            @include font(13px, 1.3rem);
            font-weight: bold;
            color: $fontColor1;
            border-bottom: 1px solid rgba(255, 255, 255, 0.4);
            width: 100%;
            text-align: left;
            padding-bottom: 14px;
            padding-left: 40px;
            margin-top: 34px;
            margin-bottom: 30px;
            position: relative;
            text-transform: uppercase;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
        }
    }
}

.formTabs {
    @include clearfix();
    list-style: none;
    padding-left: 0;
    margin: 41px auto 0;
    width: 278px;
    li {
        @include font(12px, 1.2rem);
        font-weight: bold;
        float: left;
        width: 50%;
        a {
            display: block;
            height: 40px;
            line-height: 40px;
            text-decoration: none;
            text-transform: uppercase;
            background-color: $bgColor2;
            color: $fontColor1;
            &:hover {
                opacity: 0.9;
            }
        }
        &.active {
            a {
                color: $fontColor2;
                background-color: $bgColor3;
            }
        }
        &:first-child {
            a {
                border-top-left-radius: 100px;
                border-bottom-left-radius: 100px;
            }
        }
        &:last-child {
            a {
                border-top-right-radius: 100px;
                border-bottom-right-radius: 100px;
            }
        }
    }
}

.tab-content {
    & > .tab-pane {
        display: none;
    }
    & > .active {
        display: block;
    }
}

.thankyouMsg {
    position: relative;
    h3 {
        @include font(20px, 2rem);
        color: $fontColor1;
        text-align: left;
        margin: 0 0 10px;
    }
    p {
        line-height: 29px;
    }
}

@media (max-width: 1366px) {
    .formWrapper {
        padding-top: 30px;
        padding-bottom: 30px;
        p {
            @include font(14px, 1.4rem);
            line-height: 20px;
        }
    }
    .sign-reg {
        padding-top: 25px;
    }
    .sign-reg, .not-found, .forgotPassword {
        .formWrapper {
            .logoLarge {
                display: none;
            }
            .logo {
                display: inline-block;
            }
            h2 {
                @include font(16px, 1.6rem);
                margin-top: 30px;
                margin-bottom: 10px;
            }
            h3 {
                margin-bottom: 10px;
            }
        }
    }
    .formTabs {
        margin-top: 10px;
    }
}

@media (max-width: 1200px) {
    .innerForms {
        .formWrapper {
            margin: 25px 15px;
            width: auto;
            h1 {
                display: block;
            }
        }
    }
}

@media (max-width: 991px) {
    .loggedOut {
        &.accountPages {
            .container {
                width: auto;
                margin-left: 15px;
                margin-right: 15px;
                padding-top: 90px;
            }
        }
    }
    .innerForms {
        .formWrapper {
            padding-left: 15px;
            padding-right: 15px;
            h1 {
                @include font(30px, 3rem);
                color: $fontColor1;
                margin-top: 0;
            }
            p {
                text-align: center;
            }
        }
    }
    .thankyouMsg {
        margin: 0 0 0 40px;
        p {
            text-align: left !important;
        }
    }
}

@media (max-width: 768px) {
    body {
        &.sign-reg, &.forgotPassword, &.not-found {
            background: linear-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
            background: -webkit-linear-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
            background: -webkit-gradient(-25.91deg, #FF9B8F -2.69%, #FB808E 38.26%, #E65570 90.6%);
            padding: 0;
            .cover {
                display: none;
            }
            .formWrapper {
                background: none;
                width: auto;
                padding-top: 45px;
                padding-left: 15px;
                padding-right: 15px;
                margin: 0;
                h2 {
                    margin-top: 15px;
                    margin-bottom: 5px;
                }
                p {
                    br {
                        display: none;
                    }
                }
            }
        }
    }
    .loggedOut {
        &.accountPages {
            .container {
                & > h1 {
                    @include font(30px, 3rem);
                    line-height: 40px;
                    overflow: visible;
                }
            }
        }
    }
    .innerForms {
        .formWrapper {
            p {
                margin: 5px 0 10px;
            }
        }
    }
}

@media (max-width: 480px) {
    .innerForms {
        .formWrapper {
            padding-top: 15px;
            h1 {
                @include font(20px, 2rem);
            }
        }
    }
}