@import "_shapes/shapes";

.dashboard {
    .shapes {
        background-color: $bgColor1;
        height: 170px;
        position: relative;
    }
    .content {
        padding-top: 187px;
        padding-left: 50px;
        padding-right: 50px;
        .separator {
            display: none;
            margin: 0;
        }
        h2 {
            display: none;
            @include font(12px, 1.2rem);
            font-weight: bold;
            color: $fontColor5;
            margin: 18px 0 16px;
            text-transform: uppercase;
        }
        & > .separator {
            display: block;
        }
        & > h2 {
            display: block;
        }
    }
}

@media (max-width: 1200px) {
    .dashboard {
        .shapes {
            height: 120px;
        }
        .content {
            padding-top: 150px;
            padding-left: 15px;
            padding-right: 15px;
            h2 {
                display: block;
            }
            & > .separator {
                display: none;
            }
            & > h2 {
                display: none;
            }
        }
    }
}

@media (max-width: 768px) {
    .dashboard {
        .shapes {
            height: 75px;
        }
        .content {
            padding-top: 90px;
        }
    }
}

@media (max-width: 480px) {
    .dashboard {
        .shapes {
            height: 51px;
        }
        .content {
            padding-top: 65px;
        }
    }
}

@media (max-width: 360px) {
    .dashboard {
        .shapes {
            height: 45px;
        }
        .content {
            padding-top: 58px;
        }
    }
}