.closingCtaText {
    width: 580px;
    float: left;
    padding-top: 151px;
    p {
        @include font(36px, 3.6rem);
        color: $fontColor1;
        margin: 0 0 29px;
        font-style: italic;
        font-weight: bold;
        line-height: 49px;
    }
    .btn {
        margin-top: 33px;
    }
}

.latestJobs {
    width: 448px;
    float: right;
    padding-top: 91px;
    h2 {
        @include font(12px, 1.2rem);
        color: $fontColor1;
        font-weight: bold;
        text-transform: uppercase;
        margin: 0 0 21px;
    }
    .genericTable {
        tr {
            &:last-child {
                opacity: 0.5;
            }
            &:hover {
                td {
                    border-color: $bgColor1;
                    &:first-child {
                        border-color: $bgColor1;
                    }
                    &:last-child {
                        border-color: $bgColor1;
                    }
                }
            }
        }
    }
}

@media (max-width: 1366px) {
    .closingCtaText {
        width: 480px;
        padding-top: 70px;
        p {
            @include font(30px, 3rem);
            line-height: 44px;
        }
        .btn {
            margin-top: 0px;
        }
    }

    .latestJobs {
        padding-top: 25px;
        width: 420px;
        h2 {
            margin: 0 0 15px;
        }
        .genericTable {
            margin-bottom: 0;
            tr {
                &:hover {
                    td {
                        padding-top: 14px;
                        padding-bottom: 14px;
                    }
                    .dot {
                        top: 19px;
                    }
                }
                td {
                    padding-top: 15px;
                    padding-bottom: 15px;
                    .dot {
                        top: 20px;
                    }
                }
            }
        }
    }
}

@media (max-width: 992px) {
    .closingCtaText {
        width: 310px;
        p {
            @include font(20px, 2rem);
            line-height: 30px;
            margin-bottom: 20px;
            br {
                display: none;
            }
        }
        .btn {
            margin-top: 0px;
        }
    }

    .latestJobs {
        width: 380px;
        .genericTable {
            tr {
                &:hover {
                    td {
                        padding-top: 15px;
                        padding-bottom: 15px;
                    }
                    .dot {
                        top: 20px;
                    }
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .closingCtaText {
        width: 100%;
        float: none;
        padding-top: 25px;
        p {
            @include font(16px, 1.6rem);
            line-height: 26px;
            margin-bottom: 10px;
        }
    }

    .latestJobs {
        overflow-x: scroll;
        width: 100%;
        .genericTable {
            table-layout: auto;
        }
    }
}