header {
  padding: 40px 50px 12px 38px;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 114px;
  width: 100%;
  z-index: 10000;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background: linear-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed;
  background: -webkit-linear-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed;
  background: -webkit-gradient(119deg, #FF9B8F 1%, #FB808E 32%, #E65570 99%) no-repeat top center fixed; }
  header:before, header:after {
    content: " ";
    display: table; }
  header:after {
    clear: both; }
  header img {
    float: left;
    position: relative;
    top: -3px; }
  header .logo-small {
    display: none; }
  header .services {
    float: right; }
    header .services span {
      font-size: 15px;
      font-size: 1.5rem;
      font-style: italic;
      color: #fff; }
    header .services a.btn-primary {
