.checkbox {
    text-align: left;
    &.oneline {
        label {
            margin-bottom: 20px;
        }
    }
    &.longline {
        label {
            margin-bottom: 20px;
        }
    }
    label {
        display: block;
        position: relative;
        @include font(13px, 1.3rem);
        line-height: 18px;
        padding-left: 55px;
        margin-top: 28px;
        cursor: pointer;
        color: $fontColor1;
        input {
            display: none;
        }
    }
}

@media (max-width: 1366px) {
    .checkbox {
        label {
            margin-top: 20px;
        }
    }
}

@media (max-width: 767px) {
    .checkbox {
        &.oneline {
            label {
                margin-top: 20px;
            }
        }
        &.longline {
            .icon-checked {
                top: 2px;
            }
        }
        label {
            margin-top: 5px;
        }
    }
}