.shapeContainer {
    width: 67%;
    position: absolute;
    top: 291px;
    right: 0;
    text-align: center;
    .shapeWrapper {
        width: 500px;
        height: 500px;
        margin: 0 auto;
        .morphSvgWrapper {
            width: 500px;
            height: 500px;
        }
    }
}

.polygonShape {
    display: none;
}

.benefits {
    position: relative;
    .shapeWrapper {
        display: none;
        width: 635px;
        float: right;
    }
}

.shapeWrapper {
    position: relative;
    .morphSvgWrapper {
        text-align: center;
        position: relative;
        overflow: hidden;
        z-index: 10;
        .ellipseShape {
            display: none;
            position: absolute;
            top: 0;
            left: 19px;
            z-index: 5;
        }
    }
    .portal {
        display: none;
        position: relative;
        left: -4px;
    }
}

@media (max-width: 1599px) {
    .shapeContainer {
        width: 57%;
        .shapeWrapper {
            width: 430px;
            height: 430px;
            .morphSvgWrapper {
                width: 430px;
                height: 430px;
                .ellipseShape {
                    width: 368px;
                }
            }
            .portal {
                width: 377px;
            }
        }
    }
}
@media (max-width: 1366px) {
    .shapeContainer {
        width: 56%;
        top: 207px;
        .shapeWrapper {
            width: 325px;
            height: 325px;
            .morphSvgWrapper {
                width: 325px;
                height: 325px;
                .ellipseShape {
                    width: 277px;
                }
            }
            .portal {
                width: 277px;
            }
        }
    }
    .shapeWrapper {
        img {
            width: 400px;
        }
    }
}

@media (max-width: 992px) {
    .shapeContainer {
        width: 56%;
        top: 170px;
        .shapeWrapper {
            width: 250px;
            height: 250px;
            .morphSvgWrapper {
                width: 250px;
                height: 250px;
                .ellipseShape {
                    width: 220px;
                    left: 9px;
                }
            }
            .portal {
                width: 220px;
            }
        }
    }
}

@media (max-width: 767px) {
    .shapeContainer {
        width: 100%;
        position: static;
        margin-top: 25px;
        .shapeWrapper {
            margin: 0 auto;
            svg {
                display: block !important;
            }
        }
    }
    .polygonShape {
        width: 250px;
        height: 250px;
        display: block;
        margin: 25px auto 0;
    }
}