.sign-reg {
    form {
        margin-top: 71px;
    }
}

.forgot {
    display: block;
    color: $fontColor1;
    @include font(13px, 1.3rem);
    font-weight: bold;
    text-decoration: none;
    margin-top: 10px;
    &:hover {
        text-decoration: underline;
    }
}

.postPreview {
    form {
        padding: 0 50px;
    }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
    background-color: #fff;
    transition: background-color 5000s ease-in-out 0s;
}

.form-control {
    border: none;
    background-image: none;
    padding: 0 25px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}
.form-control:focus {
    border-color: #66afe9;
    background-color: $bgColor1;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
}
input[type="text"], input[type="email"], input[type="password"], input[type="tel"], input[type="url"] {
    -webkit-appearance: none;
}
.form-control::-ms-clear {
    // style for input cross hiding in ie
    display: none;
}
button::-moz-focus-inner {
    padding: 0;
    margin: -1px 0 0;
}
input[type ="submit"], input[type ="button"] {
    cursor: pointer;
}
.btn {
    @include font(12px, 1.2rem);
    height: 40px;
    line-height: 40px;
    width: 139px;
    vertical-align: middle;
    border-radius: 100px;
    font-weight: bold;
    border: none;
    margin: 0;
    padding: 0;
    text-align: center;
    white-space: nowrap;
    text-transform: uppercase;
    outline: 0;
    touch-action: manipulation;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    &:hover {
        background-color: $bgColor7;
        color: $fontColor1;
        .icon:before {
            color: $fontColor1;
        }
    }
}
a.btn {
    display: inline-block;
    text-decoration: none;
}
.btn-primary {
    background-color: $bgColor2;
    color: $fontColor1;
}
.btn-white {
    background-color: $bgColor1;
    color: $fontColor2;
}
.btn[disabled] {
    cursor: default;
    cursor: not-allowed;
    opacity: 0.65;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.btn.inLoading {
    position: relative;
    color: transparent;
    span {
        color: white !important;
        position: absolute;
        left: 50%;
        margin-left: -30px;
    }
    &:focus, &:hover {
        color: transparent;
    }
}

@import "_form-group/form-group";
@import "_checkbox/checkbox";
@import "_error/error";
@import "_dropdownSelect/dropdownSelect";
@import "_applicantsFilter/applicantsFilter";

@media (max-width: 1366px) {
    .sign-reg {
        form {
            margin-top: 20px;
        }
    }
}

@media (max-width: 1200px) {
    .postPreview {
        form {
            padding: 0 15px;
        }
    }
}

@media (max-width: 480px) {
    .postPreview {
        form {
            @include clearfix();
            .btn {
                min-width: inherit;
                width: 49%;
                float: left;
                &.rightSpace {
                    margin-right: 2% !important;
                }
            }
        }
    }
}