@import "_progressBar/progressBar";

.postJob {
    padding-top: 60px;
    &.container {
        &.loggedOut {
            padding-left: 0;
            padding-top: 100px;
        }
    }
    &.noProgress {
        padding-top: 0;
    }
    .progressBar {
        & ~ .heading {
            padding-top: 60px;
        }
    }
    .heading {
        width: 600px;
        margin: 0 auto 61px;
        h1 {
            display: block;
            @include font(40px, 4rem);
            color: $fontColor2;
            text-align: center;
            font-weight: bold;
            line-height: 54px;
            margin: 47px 0 0;
        }
        p {
            @include font(15px, 1.5rem);
            color: $fontColor3;
            line-height: 29px;
            margin: 15px 0 0;
            .strong {
                font-style: italic;
            }
        }
    }
    .diamond {
        display: block;
        width: 41px;
        height: 42px;
        background: linear-gradient(143.84deg, #FD988C 17.82%, #FB808E 53.95%, #E65570 83.88%);
        background: -webkit-linear-gradient(143.84deg, #FD988C 17.82%, #FB808E 53.95%, #E65570 83.88%);
        background: -webkit-gradient(143.84deg, #FD988C 17.82%, #FB808E 53.95%, #E65570 83.88%);
        box-shadow: 4px -4px 4px rgba(217, 217, 217, 0.7);
        transform: rotate(135deg);
        margin: 0 auto 26px;
    }
    h2 {
        font-weight: bold;
        font-size: 12px;
        text-transform: uppercase;
        color: $fontColor3;
        text-align: center;
        margin: 20px 0 16px;
    }
    .line {
        width: 100px;
        opacity: 0.1;
        border-top: 1px solid #000000;
    }
    .circle {
        display: block;
        margin: 0 auto;
        height: 42px;
        width: 42px;
        border-radius: 100px;
        background: linear-gradient(153.43deg, #FD988C 16.67%, #FB808E 62.25%, #E65570 100%);
        background: -webkit-linear-gradient(153.43deg, #FD988C 16.67%, #FB808E 62.25%, #E65570 100%);
        background: -webkit-gradient(153.43deg, #FD988C 16.67%, #FB808E 62.25%, #E65570 100%);
        box-shadow: 0px 4px 4px rgba(217, 217, 217, 0.7);
    }
}

@import "_wizardForm/wizard";

/* @todo Kad. this code need to be removed after implementing main version of post a job wizard */
@import "_version-2/version-2";

@media (min-width: 1201px) {
    .postJob {
        .contentHeading {
            padding: 0;
        }
    }
}
@media (min-width: 1370px) {
    .postJob {
        padding-bottom: 200px;
    }
}
@media (max-width: 1200px) {
    .postJob {
        padding-top: 125px;
        &.noProgress {
            padding-top: 65px;
        }
        &.container {
            background-color: $bgColor1;
        }
    }
    .progressBar {
        position: absolute;
        left: 0;
        top: 65px;
    }
}

@media (max-width: 991px) {
    .postJob {
        &.container {
            &.loggedOut {
                padding-top: 65px;
            }
        }
    }
    .wizard {
        width: auto;
        margin: 0;
        padding-left: 15px;
        padding-right: 15px;
        .form-group {
            .form-control {
                width: 64%;
            }
            .description {
                width: 34%;
            }
            .locationFields {
                width: 64%;
                .form-control {
                    width: 100%;
                }
                .leftField {
                    width: 45%;
                }
                .rightField, .dropdownSelect {
                    width: 53%;
                }
            }
            .dateField {
                width: 163px !important;
            }
        }
        .textBoxGroup {
            width: 64%;
            .form-control {
                width: 100%;
            }
        }
        .salaryFields {
            width: 64%;
            .form-control {
                width: calc(100% - 130px);
            }
            .salaryType {
                label {
                    width: 20%;
                    margin-bottom: 0;
                    -webkit-box-sizing: border-box;
                    -moz-box-sizing: border-box;
                    box-sizing: border-box;
                    & + label {
                        border-left: 2px solid #fff;
                        margin-left: 0;
                    }
                    .btn {
                        min-width: auto;
                    }
                }
            }
        }
        .employmentType {
            width: 64%;
        }
    }
}

@media (max-width: 768px) {
    .postJob {
        padding-bottom: 100px;
        .heading {
            width: auto;
            padding: 0 15px;
            margin: 0 0 35px;
            h1 {
                @include font(30px, 3rem);
                line-height: 40px;
                margin-top: 15px;
            }
            p {
                line-height: 20px;
                margin-top: 10px;
                br {
                    display: none;
                }
            }
        }
    }
    .progressBar {
        @include font(10px, 1rem);
        padding-right: 50px;
        padding-left: 15px;
        text-align: left;
        .progress {
            width: 59%;
            .half {
                width: 48%;
            }
        }
        .close {
            left: auto;
            right: 15px;
            text-align: right;
        }
    }
    .wizard {
        form {
            margin-top: 30px;
        }
        .form-group {
            margin-bottom: 20px;
            .pull-left {
                float: none;
            }
            .control-label {
                @include font(25px, 2.5rem);
                line-height: 30px;
                margin-bottom: 5px;
            }
            textarea {
                &.form-control {
                    padding-top: 10px;
                    padding-bottom: 10px;
                }
            }
            .form-control {
                float: none;
                width: 100%;
                height: 40px;
                padding: 0 15px;
            }
            .description {
                float: none;
                width: 100%;
                margin-top: 10px;
                @include font(11px, 1.1rem);
                line-height: 16px;
                .btn {
                    margin-top: 10px;
                }
            }
            .locationFields {
                width: 100%;
                float: none;
                .form-control {
                    width: 100%;
                    border-radius: 6px;
                }
                .leftField {
                    width: 100%;
                }
                .rightField, .dropdownSelect {
                    width: 100%;
                }
                .dropdownSelect {
                    &.active {
                        button {
                            border-radius: 6px 6px 0 0;
                        }
                    }
                    button {
                        border-radius: 6px;
                        padding-left: 15px;
                    }
                    .dropdownBody {
                        border-radius: 0 0 6px 6px;
                    }
                    .dropdownList {
                        li {
                            padding-left: 15px;
                            padding-right: 15px;
                        }
                    }
                }
            }
            .radioInput {
                .radio {
                    margin-bottom: 5px;
                }
            }
            & > .btn {
                margin-bottom: 30px;
                min-width: 138px;
            }
        }
        .salaryFields {
            width: 100%;
            .salaryGroup {
                margin-bottom: 10px;
            }
            .dropdownSelect {
                &.active {
                    button {
                        border-top-left-radius: 20px;
                    }
                }
                button {
                    @include font(16px, 1.6rem);
                    height: 40px;
                    line-height: 40px;
                    text-align: left;
                    width: 120px;
                }
                .caret {
                    top: 19px;
                }
            }
            .form-control {
                @include font(16px, 1.6rem);
                width: calc(100% - 120px);
                height: 40px;
                line-height: inherit;
            }
            .salaryType {
                label {
                    margin-bottom: 5px;
                    .btn {
                        @include font(11px, 1.1rem);
                    }
                }
            }
        }
        .employmentType {
            width: 100%;
            label {
                width: 47.7%;
                &:last-child {
                    margin-right: 0;
                }
                .btn {
                    min-width: 100%;
                }
            }
        }
        .textBoxGroup {
            width: 100%;
            .subtext {
                position: static;
            }
        }
        .icon-thumbs {
            margin-top: 15px;
        }
        p {
            margin-bottom: 20px;
            line-height: 30px;
        }
    }
}

@media (max-width: 480px) {
    .wizard {
        .employmentType {
            label {
                width: 46%;
            }
        }
    }
}
