.benefitsText {
    width: 480px;
    float: left;
    &.firstSlide {
        padding-top: 229px;
        & ~ .shapeWrapper {
            padding-top: 142px;
            left: 124px;
        }
    }
    &.secondSlide {
        padding-top: 265px;
        & ~ .shapeWrapper {
            padding-top: 83px;
        }
    }
    &.thirdSlide {
        padding-top: 245px;
        & ~ .shapeWrapper {
            padding-top: 179px;
        }
    }
    h2 {
        color: $fontColor2;
        @include font(12px, 1.2rem);
        font-weight: bold;
        line-height: 71px;
        z-index: 10;
        margin: 0;
        text-transform: uppercase;
    }
    p {
        @include font(20px, 2rem);
        color: $fontColor3;
        line-height: 30px;
        margin: 0;
        font-style: italic;
        font-weight: bold;
        line-height: 38px;
        margin-bottom: 36px;
        u {
            text-decoration: none;
            border-bottom: 6px solid #F2E991;
        }
    }
}

.benefitsNav {
    display: none;
    @include clearfix();
    position: absolute;
    text-align: center;
    left: 50%;
    top: 140px;
    margin: 0 0 0 -210px;
    z-index: 100;
    padding: 0;
    -moz-user-select: none;
    -webkit-user-select: none;
    li {
        display: none;
        width: 139px;
        height: 40px;
        line-height: 40px;
        background: $bgColor2;
        color: $fontColor1;
        @include font(12px, 1.2rem);
        font-weight: bold;
        text-decoration: none;
        text-transform: uppercase;
        cursor: pointer;
        &:hover {
            opacity: 0.8;
        }
        &.active {
            background: $bgColor6;
        }
        &.firstLink, &.secondLink, &.thirdLink {
            float: left;
            display: block;
        }
        &.firstLink {
            border-radius: 100px 0 0 100px;
            margin-right: 2px;
        }
        &.thirdLink {
            border-radius: 0 100px 100px 0;
            margin-left: 2px;
        }
    }
}

@media (max-width: 1599px) {
    .benefitsText {
        &.firstSlide {
            padding-top: 205px;
            & ~ .shapeWrapper {
                padding-top: 115px;
                left: 150px;
            }
        }
        &.secondSlide {
            padding-top: 205px;
            & ~ .shapeWrapper {
                padding-top: 25px;
            }
        }
        &.thirdSlide {
            padding-top: 205px;
            & ~ .shapeWrapper {
                padding-top: 125px;
            }
        }
    }
}
@media (max-width: 1366px) {
    .benefitsText {
        & ~ .shapeWrapper {
            width: 420px;
        }
        &.firstSlide {
            padding-top: 100px;
            & ~ .shapeWrapper {
                padding-top: 100px;
                left: 56px;
            }
        }
        &.secondSlide {
            padding-top: 125px;
            & ~ .shapeWrapper {
                padding-top: 85px;
                img {
                    width: 420px;
                }
            }
        }
        &.thirdSlide {
            padding-top: 100px;
            & ~ .shapeWrapper {
                padding-top: 125px;
            }
        }
    }
}

@media (max-width: 992px) {
    .benefitsText {
        width: 320px;
        & ~ .shapeWrapper {
            width: 380px;
            img {
                width: 300px;
            }
        }
        &.secondSlide {
            & ~ .shapeWrapper {
                img {
                    width: 350px;
                }
            }
        }
        &.thirdSlide {
            & ~ .shapeWrapper {
                padding-top: 100px;
                img {
                    width: 350px;
                }
            }
        }
        p {
            @include font(14px, 1.4rem);
            line-height: 26px;
            br {
                display: none;
            }
        }
    }

    .benefitsNav {
        top: 91px;
    }
}

@media (max-width: 767px) {
    .benefitsText {
        width: 100%;
        z-index: 10;
        position: relative;
        float: none !important;
        padding-top: 25px !important;
        &.firstSlide, &.secondSlide, &.thirdSlide {
            & ~ .shapeWrapper {
                display: block !important;
                width: 250px;
                height: 250px;
                position: static;
                margin: 25px auto 0;
                float: none;
                padding: 0;
                img {
                    width: 100%;
                }
            }
        }
        h2 {
            line-height: 20px;
        }
        p {
            @include font(14px, 1.4rem);
            line-height: 26px;
            margin-bottom: 15px;
        }
    }
    .benefitsNav {
        display: none !important;
    }
}