.ellipse {
    background: linear-gradient(-26.57deg, #FD988C 16.67%, #FB808E 62.25%, #E65570 100%);
    background: -webkit-linear-gradient(-26.57deg, #FD988C 16.67%, #FB808E 62.25%, #E65570 100%);
    background: -webkit-gradient(-26.57deg, #FD988C 16.67%, #FB808E 62.25%, #E65570 100%);
    border-radius: 100%;
    position: absolute;
    left: 143px;
    bottom: -130px;
    height: 246px;
    width: 246px;
}

.hexagon {
    background-image: url(../../images/empl/hexagon.svg);
    margin-left: -107px;
    position: absolute;
    bottom: -130px;
    left: 50%;
    height: 246px;
    width: 214px; 
}

.square {
    background: linear-gradient(143.17deg, #FD988C 18.04%, #FB808E 54.05%, #E65570 83.88%);
    background: -webkit-linear-gradient(143.17deg, #FD988C 18.04%, #FB808E 54.05%, #E65570 83.88%);
    background: -webkit-gradient(143.17deg, #FD988C 18.04%, #FB808E 54.05%, #E65570 83.88%);
    transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    position: absolute;
    right: 180px;
    bottom: -94px;
    width: 174px;
    height: 174px;
    .statusInfo {
        -moz-transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        top: 45px;
    }
}

.statusInfo {
    font-weight: bold;
    color: $fontColor1;
    margin: 0 auto;
    text-align: center;
    position: relative;
    top: 59px;
    text-transform: uppercase;
    .separator {
        border-bottom: 1px solid #fff;
        margin-bottom: 13px;
        margin-top: 12px;
        width: 122px;
        border: 0;
        opacity: 0.5;
    }
    .number {
        display: block;
        @include font(60px, 6rem);
        line-height: 62px;
    }
    .title {
        display: block;
        @include font(12px, 1.2rem);
    }
}

@media (max-width: 1366px) {
    .ellipse {
        left: 90px;
    }
    .square {
        right: 126px;
    }
}

@media (max-width: 1200px) {
    .ellipse {
        bottom: -100px;
        left: 40px;
        height: 200px;
        width: 200px;
        .statusInfo {
            top: 27px;
        }
    }
    .hexagon {
        background-size: 100%;
        margin-left: -87px;
        bottom: -100px;
        width: 174px;
        height: 200px;
        .statusInfo {
            top: 27px;
        }
    }
    .square {
        right: 76px;
        bottom: -71px;
        height: 142px;
        width: 142px;
        .statusInfo {
            top: 32px;
        }
    }
}

@media (max-width: 768px) {
    .ellipse {
        left: 8%;
        bottom: -71px;
        height: 130px;
        width: 130px;
        .statusInfo {
            top: 34px;
        }
    }
    .hexagon {
        margin-left: -65px;
        bottom: -71px;
        height: 130px;
        width: 113px;
        .statusInfo {
            top: 34px;
        }
    }
    .square {
        width: 92px;
        height: 92px;
        right: 15%;
        bottom: -52px;
        .statusInfo {
            top: 18px;
            left: 6px;
        }
    }
    .statusInfo {
        .separator, .secondary {
            display: none;
        }
        .number {
            @include font(20px, 2rem);
            line-height: 24px;
        }
        .title {
            @include font(11px, 1.1rem);
        }
    }
}

@media (max-width: 480px) {
    .ellipse {
        width: 95px;
        height: 95px;
        left: 9%;
        bottom: -54px;
        .statusInfo {
            top: 22px;
        }
    }
    .hexagon {
        width: 83px;
        height: 95px;
        margin-left: -42px;
        bottom: -54px;
        .statusInfo {
            top: 22px;
        }
    }
    .square {
        width: 68px;
        height: 67px;
        right: 13%;
        bottom: -40px;
        .statusInfo {
            top: 12px;
            left: 6px;
        }
    }
    .statusInfo {
        .number {
            @include font(16px, 1.6rem);
            line-height: 20px;
        }
        .title {
            @include font(9px, 0.9rem);
        }
    }
}

@media (max-width: 360px) {
    .ellipse {
        width: 80px;
        height: 80px;
        left: 10%;
        bottom: -46px;
    }
    .hexagon {
        width: 70px;
        height: 80px;
        margin-left: -35px;
        bottom: -46px;
    }
    .square {
        width: 57px;
        height: 57px;
        right: 14%;
        bottom: -35px;
        .statusInfo {
            top: 0px;
            left: 0px;
        }
    }
    .statusInfo {
        padding: 8px 5px;
        position: static;
        .title {
            @include font(10px, 1rem);
        }
    }
}